

import Vue from 'vue';

import App from './App';

import store from './store/index';
import router from './router/index';

import global from '@/assets/js/global';
Vue.prototype.GLOBAL = global;

import './assets/css/index.css';
import './assets/css/reset.css';
import './assets/css/resetweui.less';
import './assets/css/iconfont.css';


/**
* 全局函数，获取广告图
* @params:type
* 1-首页
* 2-带走的
* 3-商城首页
* 4-景区消费
* 5-景区好礼
* 6-名家妙藏
* 7-变现首页
* 8-艺品回购
* 9-艺品租赁
* 10-艺品寄卖
* 11-信息消费
* 12-创收首页
* 13-才源共享
*/
import { getBannerImg } from "@/assets/js/common";
Vue.prototype.getBannerImgFn = getBannerImg;

/**
* 导航默认行为类似手机APP的页面导航（A、B、C为页面）：
* A前进到B，再前进到C；
* C返回到B时，B会从缓存中恢复；
* B再次前进到C，C会重新生成，不会从缓存中恢复；
* C前进到A，A会生成，现在路由中包含2个A实例
* */
import Navigation from 'vue-navigation';
Vue.use(Navigation, { router, store, moduleName: 'navigation', keyName: 'KC' });


import VueLazyload from 'vue-lazyload';
Vue.use(VueLazyload);


import Vuex from 'vuex';
Vue.use(Vuex);


import WechatPlugin from 'vux/src/plugins/wechat/index.js'

Vue.use(WechatPlugin);


import VueBetterScroll from 'vue2-better-scroll';
Vue.use(VueBetterScroll);


import vuei18n from 'vuex-i18n';
Vue.use(vuei18n);


import ViewBox from 'vux/src/components/view-box/index.vue'

Vue.component('view-box', ViewBox);


import VueScroller from 'vue-scroller';
Vue.use(VueScroller);


import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import ConfigPlugin from 'vux/src/plugins/config/index.js'

Vue.use(ToastPlugin);
Vue.use(LoadingPlugin);
Vue.use(ConfirmPlugin);
Vue.use(ConfigPlugin, {
$layout: 'VIEW_BOX'
});


import FastClick from 'fastclick';
FastClick.attach(document.body);


import VueCordova from 'vue-cordova';
Vue.use(VueCordova);


import { eventBackButton } from '@/assets/js/quitapp';
eventBackButton();


import VueAMap from 'vue-amap';
import { lazyAMapApiLoaderInstance } from 'vue-amap';
Vue.prototype.AMap = VueAMap;
Vue.use(VueAMap);
VueAMap.initAMapApiLoader({
key: '956cab63ef78320624fb7d7dff4c25b5',
plugin: ['AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType', 'AMap.Autocomplete', 'AMap.PlaceSearch', 'AMap.PolyEditor', 'AMap.CircleEditor', 'AMap.Geolocation'],
uiVersion: '1.0', // ui库版本，不配置不加载
v: '1.4.4'
});

Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
router,
store,
data: () => {
return {
cordova: Vue.cordova
}
},
render: h => h(App)
}).$mount('#app-box')



