
import { vueCordovaFunction } from "@/assets/js/vuecordova";
import Loading from 'vux/src/components/loading/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import { CheckByLocation } from "@/servers/api";
import { appVersion,updateAPPVersion } from "@/assets/js/common";
export default {
name: "App",
data() {
return {
transitionName: "slide-right"
};
},
components: {
Loading,
Confirm
},
mounted() {
vueCordovaFunction.getLocation();
appVersion();
},
beforeRouteUpdate(to, from, next) {
next(vm => {
console.log(vm);
});
},
computed: {
isLoading() {
return this.$store.state.isLoading;
},
showModel: {
get: function() {
return this.$store.state.showActionDialog;
},
set: function() {}
},
isForceUpdata: {
get: function() {
return this.$store.state.appUpdateInfo.isForce;
}
},
modelContent: {
get: function() {
return this.$store.state.appUpdateInfo.content;
}
}
},
methods: {
onCancel() {},
onConfirm() {
updateAPPVersion();
}
},
watch: {
$route(to, from) {
let isBack = this.$router.isBack;
if (isBack) {
this.transitionName = "slide-right";
} else {
this.transitionName = "slide-left";
}
this.$router.isBack = false;
}
}
};



